import "../../../../chunks/index.js";
import { d as db } from "../../../../chunks/db.server.js";
async function POST(evt) {
  const data = await evt.request.json();
  data.phase1.phase = 1;
  data.phase2.phase = 2;
  data.phase3.phase = 3;
  await db.sample.create({
    data: {
      PhaseData: {
        create: [data.phase1, data.phase2, data.phase3]
      }
    }
  });
  const digital_outputs = await db.digitalOutput.findMany();
  return new Response(
    JSON.stringify({
      success: true,
      digital_outputs
    }),
    {
      headers: {
        "Content-Type": "application/json"
      }
    }
  );
}
export {
  POST
};
